import React, { useEffect, useState } from "react";
import { MDXProvider } from "@mdx-js/react";
import Prism from "prismjs";

// Prism.js ÌÖåÎßàÏôÄ Ïñ∏Ïñ¥ ÏßÄÏõê Ï∂îÍ∞Ä
import "../styles/prism-custom.css"; // Ïª§Ïä§ÌÖÄ Gatsby Ïä§ÌÉÄÏùº ÌÖåÎßà
import "prismjs/components/prism-javascript";
import "prismjs/components/prism-typescript";
import "prismjs/components/prism-jsx";
import "prismjs/components/prism-tsx";
import "prismjs/components/prism-css";
import "prismjs/components/prism-bash";
import "prismjs/components/prism-json";
import "prismjs/components/prism-java";

// Ïñ∏Ïñ¥Î≥Ñ ÏÉâÏÉÅ ÎßµÌïë
const getLanguageInfo = (language) => {
  const languageMap = {
    javascript: {
      color: "bg-yellow-100 text-yellow-800 border-yellow-200",
      name: "JavaScript",
    },
    typescript: {
      color: "bg-blue-100 text-blue-800 border-blue-200",
      name: "TypeScript",
    },
    jsx: {
      color: "bg-yellow-100 text-yellow-800 border-yellow-200",
      name: "JSX",
    },
    tsx: {
      color: "bg-blue-100 text-blue-800 border-blue-200",
      name: "TSX",
    },
    css: {
      color: "bg-blue-100 text-blue-800 border-blue-200",
      name: "CSS",
    },
    bash: {
      color: "bg-gray-100 text-gray-800 border-gray-200",
      name: "Bash",
    },
    shell: {
      color: "bg-gray-100 text-gray-800 border-gray-200",
      name: "Shell",
    },
    json: {
      color: "bg-yellow-100 text-yellow-800 border-yellow-200",
      name: "JSON",
    },
    html: {
      color: "bg-orange-100 text-orange-800 border-orange-200",
      name: "HTML",
    },
    java: {
      color: "bg-red-100 text-red-800 border-red-200",
      name: "Java",
    },
  };

  return (
    languageMap[language] || {
      color: "bg-gray-100 text-gray-800 border-gray-200",
      name: language || "text",
    }
  );
};

// Î≥µÏÇ¨ Î≤ÑÌäº Ïª¥Ìè¨ÎÑåÌä∏
const CopyButton = ({ code }) => {
  const [copied, setCopied] = useState(false);

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(code);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    } catch (err) {
      console.error("Î≥µÏÇ¨Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§:", err);
    }
  };

  return (
    <button
      onClick={handleCopy}
      className={`
        group relative px-3 py-1.5 rounded-md text-xs font-medium transition-all duration-200
        ${
          copied
            ? "bg-purple-700 hover:bg-purple-800 text-white shadow-md"
            : "bg-white hover:bg-gray-50 text-gray-600 hover:text-gray-800 border border-gray-200 hover:border-gray-300 shadow-sm hover:shadow-md"
        }
        focus:outline-none focus:ring-2 focus:ring-blue-500/20 active:scale-95
        dark:bg-gray-700 dark:hover:bg-gray-600 dark:text-gray-300 dark:border-gray-600
      `}
      title={copied ? "Î≥µÏÇ¨ ÏôÑÎ£å!" : "ÏΩîÎìú Î≥µÏÇ¨"}
    >
      {copied ? (
        <span className="flex items-center gap-1.5">
          <svg className="w-3.5 h-3.5" fill="currentColor" viewBox="0 0 20 20">
            <path
              fillRule="evenodd"
              d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
              clipRule="evenodd"
            />
          </svg>
          <span className="hidden sm:inline">Î≥µÏÇ¨Îê®</span>
        </span>
      ) : (
        <span className="flex items-center gap-1.5">
          <svg
            className="w-3.5 h-3.5 group-hover:scale-110 transition-transform"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={1.5}
              d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z"
            />
          </svg>
          <span className="hidden sm:inline">Î≥µÏÇ¨</span>
        </span>
      )}
    </button>
  );
};

const components = {
  h2: (props) => {
    const id = props.children
      .replace(/\s/g, "-")
      .replace(/\//g, "")
      .toLowerCase();
    return (
      <h2
        className="mt-12 text-3xl text-violet-900 font-semibold"
        id={id}
        {...props}
      />
    );
  },
  h3: (props) => {
    const id = props.children
      .replace(/\s/g, "-")
      .replace(/\//g, "")
      .toLowerCase();
    return (
      <h3
        className="mt-7 text-2xl text-violet-900 font-semibold"
        id={id}
        {...props}
      />
    );
  },
  a: (props) => <a className="text-violet-900 hover:underline" {...props} />,
  p: (props) => <p className="mt-4 text-base leading-7" {...props} />,
  blockquote: (props) => (
    <blockquote
      className="mt-4 border-l-4 border-slate-200 pl-4 italic"
      {...props}
    />
  ),
  hr: (props) => <hr className="my-8 border-t border-slate-200" {...props} />,
  ul: (props) => (
    <ul className="mt-4 pl-5 list-disc text-base leading-7" {...props} />
  ),
  ol: (props) => <ol className="mt-4 list-decimal list-inside" {...props} />,
  li: (props) => <li className="mt-2" {...props} />,
  pre: (props) => {
    const { children, className } = props;

    let language = "text";
    let codeText = "";

    // Îçî Í∞ïÎ†•Ìïú Ïñ∏Ïñ¥ Ï∂îÏ∂ú Î°úÏßÅ
    const extractLanguageAndCode = (element) => {
      if (React.isValidElement(element)) {
        // code ÏöîÏÜåÏùò classNameÏóêÏÑú Ïñ∏Ïñ¥ Ï∂îÏ∂ú
        if (element.props?.className) {
          const match = element.props.className.match(/language-(\w+)/);
          if (match) {
            return { language: match[1], found: true };
          }
        }

        // Ïû¨Í∑ÄÏ†ÅÏúºÎ°ú children ÌôïÏù∏
        if (element.props?.children) {
          const childResult = extractLanguageAndCode(element.props.children);
          if (childResult.found) {
            return childResult;
          }
        }
      } else if (Array.isArray(element)) {
        for (const child of element) {
          const result = extractLanguageAndCode(child);
          if (result.found) {
            return result;
          }
        }
      }

      return { language: "text", found: false };
    };

    // Ïñ∏Ïñ¥ Ï∂îÏ∂ú ÏãúÎèÑ
    const result = extractLanguageAndCode(children);
    if (result.found) {
      language = result.language;
    }

    // classNameÏóêÏÑúÎèÑ ÌôïÏù∏ (fallback)
    if (language === "text" && className) {
      const match = className.match(/language-(\w+)/);
      if (match) {
        language = match[1];
      }
    }

    // ÏΩîÎìú ÌÖçÏä§Ìä∏ Ï∂îÏ∂ú
    const extractCode = (element) => {
      if (typeof element === "string") {
        return element;
      } else if (React.isValidElement(element)) {
        if (element.props?.children) {
          return extractCode(element.props.children);
        }
      } else if (Array.isArray(element)) {
        return element.map(extractCode).join("");
      }
      return "";
    };

    codeText = extractCode(children);

    const languageInfo = getLanguageInfo(language);

    return (
      <div className="my-8 rounded-lg overflow-hidden border border-gray-200 dark:border-gray-700 shadow-lg hover:shadow-xl transition-shadow duration-300">
        {/* Ìó§Îçî */}
        <div className="bg-gradient-to-r from-gray-50 to-gray-100 dark:from-gray-800 dark:to-gray-700 px-4 py-3 border-b border-gray-200 dark:border-gray-600">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              {/* ÌÑ∞ÎØ∏ÎÑê Ïä§ÌÉÄÏùº Î≤ÑÌäºÎì§ */}
              <div className="flex items-center gap-1.5">
                <div className="w-3 h-3 rounded-full bg-red-500 hover:bg-red-600 transition-colors cursor-pointer"></div>
                <div className="w-3 h-3 rounded-full bg-yellow-500 hover:bg-yellow-600 transition-colors cursor-pointer"></div>
                <div className="w-3 h-3 rounded-full bg-green-500 hover:bg-green-600 transition-colors cursor-pointer"></div>
              </div>
              {/* Ïñ∏Ïñ¥ ÌÉúÍ∑∏ */}
              <div
                className={`px-2.5 py-1 rounded-full text-xs font-medium border ${languageInfo.color}`}
              >
                <span>{languageInfo.name}</span>
              </div>
            </div>
            <CopyButton code={codeText} />
          </div>
        </div>

        {/* ÏΩîÎìú ÏòÅÏó≠ */}
        <div className="relative bg-gradient-to-br from-gray-50 to-white dark:from-gray-900 dark:to-gray-800">
          {/* ÏΩîÎìú Î∞∞Í≤Ω Ìå®ÌÑ¥ (ÏÑ†ÌÉùÏÇ¨Ìï≠) */}
          <div className="absolute inset-0 opacity-[0.02] bg-[url('data:image/svg+xml;base64,')]"></div>

          <pre
            className={`
              relative !p-6 !text-sm !leading-relaxed !overflow-x-auto !m-0 !border-0
              scrollbar-thin scrollbar-track-gray-100 scrollbar-thumb-gray-300
              dark:scrollbar-track-gray-800 dark:scrollbar-thumb-gray-600
              ${className || ""}
            `}
            style={{
              padding: "1.5rem !important",
              margin: "0 !important",
              border: "none !important",
              backgroundColor: "transparent",
              fontSize: "14px",
              lineHeight: "1.6",
              fontFamily:
                "'Fira Code', 'SF Mono', Monaco, 'Cascadia Code', 'Roboto Mono', Consolas, 'Courier New', monospace",
            }}
            {...props}
          >
            {children}
          </pre>
        </div>
      </div>
    );
  },
  code: (props) => {
    const { children, className } = props;

    // Ïù∏ÎùºÏù∏ ÏΩîÎìúÏù∏ÏßÄ ÏΩîÎìú Î∏îÎ°ùÏù∏ÏßÄ ÌôïÏù∏
    const isInline =
      typeof children === "string" && !children.includes("\n") && !className;

    if (isInline) {
      // Ïù∏ÎùºÏù∏ ÏΩîÎìú Ïä§ÌÉÄÏùº
      return (
        <code
          className="
            relative inline-flex items-center px-2 py-0.5 rounded-md text-sm font-mono 
            bg-gradient-to-r from-purple-50 to-pink-50 
            text-purple-800 border border-purple-200/50 
            shadow-sm hover:shadow-md transition-all duration-200
            dark:from-purple-900/20 dark:to-pink-900/20 
            dark:text-purple-200 dark:border-purple-500/30
          "
          {...props}
        />
      );
    }

    // ÏΩîÎìú Î∏îÎ°ù ÎÇ¥Î∂ÄÏùò ÏΩîÎìú (Prism.jsÍ∞Ä Ï≤òÎ¶¨)
    return <code className={className} {...props} />;
  },
};

const PostMDXProvider = ({ children }) => {
  useEffect(() => {
    // Ïª¥Ìè¨ÎÑåÌä∏Í∞Ä ÎßàÏö¥Ìä∏Îê† Îïå Prism.jsÎ°ú ÌïòÏù¥ÎùºÏù¥ÌåÖ Ï†ÅÏö©
    Prism.highlightAll();
  }, []);

  return <MDXProvider components={components}>{children}</MDXProvider>;
};

export default PostMDXProvider;
