import React, { useState, useEffect } from "react";

const TableOfContents = ({ tableOfContents }) => {
  const [activeId, setActiveId] = useState("");

  useEffect(() => {
    if (!tableOfContents?.items) return;

    // Î™®Îì† Ìó§Îî© ÏöîÏÜåÎì§Ïùò IDÎ•º ÏàòÏßë
    const headingIds = [];
    const collectIds = (items) => {
      items.forEach((item) => {
        if (item.url) {
          headingIds.push(item.url.replace("#", ""));
        }
        if (item.items) {
          collectIds(item.items);
        }
      });
    };
    collectIds(tableOfContents.items);

    // Ïä§ÌÅ¨Î°§ Ïù¥Î≤§Ìä∏Î•º ÏÇ¨Ïö©ÌïòÏó¨ ÌòÑÏû¨ Î≥¥Ïù¥Îäî Ìó§Îî© Ï∂îÏ†Å
    const handleScroll = () => {
      const scrollPosition = window.scrollY + 150; // ÏÉÅÎã® Ïó¨Î∞± Í≥†Î†§
      let currentActiveId = "";

      // Í∞Å Ìó§Îî©Ïùò ÏúÑÏπòÎ•º ÌôïÏù∏ÌïòÏó¨ ÌòÑÏû¨ Î≥¥Ïù¥Îäî ÏÑπÏÖò Í≤∞Ï†ï
      for (const id of headingIds) {
        const element = document.getElementById(id);
        if (element) {
          const elementTop = element.offsetTop;
          if (elementTop <= scrollPosition) {
            currentActiveId = id;
          } else {
            break;
          }
        }
      }

      setActiveId(currentActiveId);
    };

    // Ï¥àÍ∏∞ ÏÉÅÌÉú ÏÑ§Ï†ï
    handleScroll();

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, [tableOfContents]);

  if (!tableOfContents || !tableOfContents.items) {
    return null;
  }

  const renderItems = (items) => {
    return (
      <ul className="text-sm text-slate-600">
        {items.map((item, index) => {
          const isActive = item.url === `#${activeId}`;
          return (
            <li key={index} className="mt-1">
              <a
                href={item.url}
                className={`block py-1 transition-all duration-200 ${
                  isActive
                    ? "text-violet-700 font-semibold border-l-2 border-violet-700 pl-2 -ml-2 bg-violet-50"
                    : "hover:text-violet-700 hover:pl-1 hover:-ml-1"
                }`}
                dangerouslySetInnerHTML={{ __html: item.title }}
                onClick={(e) => {
                  e.preventDefault();
                  const targetId = item.url.replace("#", "");
                  const element = document.getElementById(targetId);
                  if (element) {
                    element.scrollIntoView({
                      behavior: "smooth",
                      block: "start",
                    });
                  }
                }}
              />
              {item.items && (
                <div className="pl-4">{renderItems(item.items)}</div>
              )}
            </li>
          );
        })}
      </ul>
    );
  };

  return (
    <aside className="hidden lg:fixed lg:top-24 lg:left-[calc(50%+384px+2rem)] lg:w-[256px] lg:block">
      <nav>
        <div className="mt-2">{renderItems(tableOfContents.items)}</div>
      </nav>
    </aside>
  );
};

export default TableOfContents;
